import uniPopup from '@/components/uni-popup/uni-popup.vue'
import uniPopupMessage from '@/components/uni-popup/uni-popup-message.vue'
export default {
	data() {
		return {
			remPwd: false,
			name: '',
			pwd: '',
			showPassword: true,
			message: ''
		}
	},
	components: {
		uniPopup,
		uniPopupMessage
	},
	onLoad() {
		this.getNamePwd()
	},
	methods: {
		//查看和隐藏密码
		changePassword: function() {
			this.showPassword = !this.showPassword;
		},
		// 记住密码勾选和取消
		remChange: function(e) {
			const len = e.detail.value.length
			this.remPwd = !!len
		},
		// 判断缓存看是否填充用户名和密码
		getNamePwd: function() {
			const Hname = uni.getStorageSync('Hname');
			const Hpwd = uni.getStorageSync('Hpwd');
			if (Hname && Hpwd) {
				this.name = Hname;
				this.pwd = Hpwd;
				this.remPwd = true
			}
		},
		login: function() {
			const $this = this
			const {
				name,
				pwd
			} = this
			// 先判断是否用户名密码都填了
			if (name && pwd) { // 然后判断勾选了记住密码没
				uniCloud.callFunction({
						name: 'user',
						data: {
							name,
							pwd
						}
					})
					.then(res => {
						const {
							code,
							msg
						} = res.result

						if (code === 201) {
							uni.setStorageSync('Hname', $this.remPwd ? name : '')
							uni.setStorageSync('Hpwd', $this.remPwd ? pwd : '')
							console.log(222222)
							uni.navigateTo({
								url: '/pages/index/index?id=3'
							})
						} else {
							this.message = msg
							$this.$refs.popup.open()
						}
					})
			} else {
				this.message = '请输入用户名和密码！'
				this.$refs.popup.open()
			}
		}
	}
}
